import {useEffect} from 'react'
import { useLocation } from 'react-router-dom'

export default function ScrollToTop() {

    const {pathname} = useLocation()

    useEffect(() => {
        document.documentElement.scrollTo({
            top: 0,
            left: 0,
            behavior: "instant"
        })

        if (!pathname.includes('products/')) {
            document.title='MAX'
        }
    }, [pathname])
  return null
}
