import './ProductCard.scss'
import CardGiftcardIcon from '@mui/icons-material/CardGiftcard';
import RocketIcon from '@mui/icons-material/Rocket';
import StarIcon from '@mui/icons-material/Star';
import { Link } from 'react-router-dom';

function ProductCard(props) {
    let price = String(Number(props.product.price) * (100 - props.product.discount_percent) / 100)

    return (
        <Link to={`/products/${props.product.product_id}`} className='product_card'>
            <img src={props.product.img_url} alt={props.product.title} />
            {(props.product.discount_percent && props.product.isExist) ? <span className='discount_percent'>{props.product.discount_percent}%</span> : null}
            <div className="product-detail">
                <h3 className='card_title'>{props.product.title}</h3>
                <div className="row1">
                    <span className='score'><StarIcon className='star-icon' /> {props.product.score}</span>
                    {props.product.option === 'delivery' ? <span className='option'>   <RocketIcon className='rocket-icon' /></span> :
                        (!props.product.option) ? <span className='option'>     <CardGiftcardIcon className='gift-icon' /></span> : null}
                </div>
                <div className="price-box">
                    {!props.product.isExist && <span className='text'> </span>}
                    {props.product.isExist ? <span>
                        {(Number(price) < 1_000_000) ? <>{String(price).slice(0, -3)},{String(price).slice(-3)}</>
                            : <>{String(price).slice(0, -6)},{price.slice(-6, -3)},{price.slice(-3)}</>}
                        <em></em>
                    </span> : null}
                    {(props.product.discount_percent && props.product.isExist) ? <del>
                        {Number(props.product.price) < 1_000_000 ? <>{String(props.product.price).slice(0, -3)},{String(props.product.price).slice(-3)}</>
                            : <>{String(props.product.price).slice(0, -6)},{String(props.product.price).slice(-6, -3)},{String(props.product.price).slice(-3)}</>}
                    </del> : null}
                </div>
            </div>
        </Link>
    )
}

export default ProductCard