import React, { useState } from 'react'
import MenuIcon from '@mui/icons-material/Menu';
import WhatshotIcon from '@mui/icons-material/Whatshot';
import PercentIcon from '@mui/icons-material/Percent';
import FiberNewIcon from '@mui/icons-material/FiberNew';
import DiscountIcon from '@mui/icons-material/Discount';
import HandshakeIcon from '@mui/icons-material/Handshake';
import ContactSupportIcon from '@mui/icons-material/ContactSupport';
import CloseIcon from '@mui/icons-material/Close';
import DevicesIcon from '@mui/icons-material/Devices';
import HandymanIcon from '@mui/icons-material/Handyman';
import ChairIcon from '@mui/icons-material/Chair';
import ToysIcon from '@mui/icons-material/Toys';
import ModeIcon from '@mui/icons-material/Mode';
import BoyIcon from '@mui/icons-material/Boy';
import FastfoodIcon from '@mui/icons-material/Fastfood';
import { Link } from 'react-router-dom';
import ArrowLeftIcon from '@mui/icons-material/ArrowLeft';
import './Navbar.css';
import HelpIcon from '@mui/icons-material/Help';

function Navbar() {
    const [navbar_state, setNavbar_state] = useState('close')
    const [windowWidth, setWindowWidth] = useState(window.innerWidth)

    window.addEventListener('resize', () => {
        setWindowWidth(window.innerWidth)
    })
    function navbar_handler(st) {
        setNavbar_state(st)
    }
    return (
        <>
            {windowWidth < 992 && <div className='TopNav-container'>
                <MenuIcon onClick={() => navbar_handler('open')}/>
                <img src="/images/MAX-Fashions.png" alt="logo" />
                <HelpIcon />
            </div>}

            <nav className={`nav ${navbar_state === 'open' ? 'responsive' : null}`}>
                <CloseIcon className='close-icon' onClick={() => navbar_handler('close')}/>
                <Link to="/"><img src="/images/MAX-Fashions.png" alt="logo" /></Link>
                <a href='#' className="mainItem">
                    <MenuIcon />
                    <span>  </span>
                    <div className="drop_down_menu">
                        <Link to={'/category/mobile'} className='part'><DevicesIcon /><span>     </span></Link>
                        <Link to={"/category/supermarket"} className='part'><FastfoodIcon /><span>    </span></Link>
                        <Link to={'/category/apparel'} className='part drop'><BoyIcon /><span>    <ArrowLeftIcon /></span>
                            <div className="drop_down_menu_2">
                                <Link to={'/category/apparel'} className='one'>  </Link>
                                <Link to={'/category/apparel'}>   </Link>
                                <Link to={'/category/apparel'}> </Link>
                                <Link to={'/category/apparel'}>  </Link>
                            </div>
                        </Link>
                        <span className='part'><ChairIcon /><span>   </span></span>
                        <Link to={'/category/stationery'} className='part'><ModeIcon /><span>     </span></Link>
                        <span className='part'><HandymanIcon /><span>   </span></span>
                        <span className='part'><ToysIcon /><span>   </span></span>
                    </div>
                </a>
                <span className='separator'></span>
                <Link to='/category/supermarket' onClick={() => navbar_handler('close')}>
                    <FiberNewIcon className='icon icon1' /><span> </span>
                </Link>
                <Link to='/incredible-offers/11' onClick={() => navbar_handler('close')}>
                    <WhatshotIcon className='icon icon2' /><span> </span>
                </Link>
                <Link to='/incredible-offers/11' onClick={() => navbar_handler('close')}>
                    <DiscountIcon className='icon icon3' /><span></span>
                </Link>
                <Link to="/incredible-offers/11" onClick={() => navbar_handler('close')}>
                    <PercentIcon className='icon icon4' /><span> </span>
                </Link>
                <span className='separator'></span>
                <Link to='/incredible-offers/11' onClick={() => navbar_handler('close')}>
                    <ContactSupportIcon className='icon icon5' /><span> </span>
                </Link>
                <Link to='/incredible-offers/11' onClick={() => navbar_handler('close')}>
                    <HandshakeIcon className='icon icon6' /><span></span>
                </Link>
            </nav>
        </>
    )
}

export default Navbar 