import React from 'react'
import CategoryItem from './Category_item'
import './Category_section.css'

function CategorySection() {

    const categories = [
        {address: 'https://google.com', num: 1, text: '   '},
        {address: 'https://google.com', num: 2, text: '   '},
        {address: 'https://google.com', num: 3, text: ' '},
        {address: 'https://google.com', num: 4, text: ' '},
        {address: 'https://google.com', num: 5, text: ''},
        {address: 'https://google.com', num: 6, text: '  '},
        {address: 'https://google.com', num: 7, text: '  '}
    ]

    return (
        <section className='category_container_section'>
            <h5 className='title'>  </h5>
            <div className="category_items_container">
                { categories.map(item => <CategoryItem key={item.num} item_num={item.num} address={item.address} text={item.text}/>) }
                {/* <CategoryItem address='https://google.com' item_num={1} text='   ' />
                <CategoryItem address='https://google.com' item_num={2} text='   ' /> */}
            </div>
        </section>
    )
}

export default CategorySection